import { defineComponent, ref, watch } from 'vue';
export default defineComponent({
    name: 'PopupWithHead',
    props: {
        show: {
            type: Boolean,
            requrie: true,
            default: false
        },
        headImg: {
            type: String,
            requrie: true,
            default: ''
        },
        headImgStyle: {
            type: Object || null,
            requrie: false,
            default: null
        },
        needCancelBtn: {
            type: Boolean,
            requrie: true,
            default: false
        },
        contentText: {
            type: String,
            requrie: false,
            default: ''
        },
        confirmBtnText: {
            type: String,
            requrie: true,
            default: '确定'
        },
        needCloseBtn: {
            type: Boolean,
            requrie: false,
            default: false
        }
    },
    setup(props, context) {
        const showPopup = ref(props.show);
        watch(() => props.show, () => {
            showPopup.value = props.show;
        });
        const handleClickConfirm = () => {
            context.emit('confirm');
        };
        const handleClickCancel = () => {
            context.emit('cancel');
        };
        return {
            showPopup,
            handleClickConfirm,
            handleClickCancel
        };
    }
});
