import { http } from '@/http';
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { defineComponent, ref } from 'vue';
import PopupWithHead from '@/components/PopupWithHead/index.vue';
export default defineComponent({
    name: 'EditInvition',
    components: { PopupWithHead },
    props: {
        titleP: {
            type: String,
            requried: true,
            default: ''
        },
        subTitleP: {
            type: String,
            requried: true,
            default: ''
        },
        contentP: {
            type: String,
            requried: true,
            default: ''
        }
    },
    setup(props) {
        const title = ref(props.titleP);
        const subTitle = ref(props.subTitleP);
        const msgContent = ref(props.contentP);
        const showPopup = ref(false);
        const handleSubmit = () => {
            if (!title.value || !subTitle.value || !msgContent.value) {
                Toast.fail('请完整填写表单');
                return;
            }
            const params = {
                title: title.value,
                subTitle: subTitle.value,
                msgContent: msgContent.value
            };
            http
                .post('/hbSeller/sellerInvite/createInviteMsg', params, true)
                .then((res) => {
                if (res.code === '200') {
                    showPopup.value = true;
                }
                else {
                    Toast.fail(res.msg);
                }
            });
        };
        return {
            title,
            subTitle,
            msgContent,
            showPopup,
            handleSubmit
        };
    }
});
